/* Add this to your existing App.css */

/* Modify the existing .backdrop style in your App.css */
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  z-index: 1;
}

/* Modify the existing .modal style in your App.css */
.modal {
  background-color: green;
  margin: auto;
  left: 26%;
  position: fixed;
  width: 50%;
  height: auto;
  background: white;
  padding: 1rem;
  border: 4px solid green;
  border-radius: 10px;
}

/* Add this style to disable pointer events when the modal is open */
.App.disable-pointer-events {
  pointer-events: none;
}

.App {
  position: relative;
  height: 100vh;
  background: #f0f0f0;
}

.add-user-button-container {
  position: absolute;
  top: 1rem;
  right: 1rem;
}

button {
  background-color: #4caf50;
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #45a049;
}

form {
  display: grid;
  gap: 0.6rem;
  width: 80%;
  margin: auto;
}

input {
  padding: 6px 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.error {
  color: red;
  font-size: 15px;
}
